@mixin body-position {
    max-width: $wmax;
    min-width: $wmin;
    margin: auto;
  };
  
@mixin linear-gradient {
  background: linear-gradient(rgba(255, 255, 255, 0) 0%, #00000080);
  };
  
@mixin WH-full {
  width: 100%;
  height: 100%;
  };
  
@mixin flexend {
  display: flex;
  align-items: center;
  justify-content: flex-end;
};
  
@mixin flexrow{
  display: flex;
  flex-direction: row;
};

@mixin margin{
  margin: 0 7% 0 7%;
}

@mixin mobile{
  @media screen and (max-width: map-get($breakpoints, mobile-landscape)){
    @content;
  }
}