.nav-header {
  @include flexend;
  text-transform: uppercase;
  
  @media screen and (min-width: $breakpoint-desktop) {
    text-transform: none;
  }
  
  
  &__nav-link-home, &__nav-link-aboutus{
    margin-left: 2rem;
    text-decoration: none;
    color: $primary-color;
    display: inline-block;
    position: relative;
    color: $primary-color;
    &:after{
      content: '';
      position: absolute;
      width: 100%;
      transform: scaleX(0);
      height: 2px;
      bottom: 0;
      left: 0;
      background-color: $primary-color;
      transform-origin: bottom right;
      transition: transform 0.25s ease-out;
    }
    &:hover::after{
      transform: scaleX(1);
      transform-origin: bottom left;
    }
  }
}